import React, { Component } from 'react'
import { Radio, Row, Col, Form, Input, Modal } from 'antd'
// import {postAction} from 'src/axios';
const RadioGroup = Radio.Group
const FormItem = Form.Item
const formItemLayout = {
    labelCol: { span: 6},
    wrapperCol: { span: 14, style:{paddingLeft:'20px'}},
};
const handleResultTypes = ['请假','实习','休学','退学','毕业','其他'];

class HandleStateModal extends Component {
    saveAction = ()=> {
        this.props.form.validateFields((err,values) => {
            if (!err) {
                // let { handleResult, handleRemark } = values;
                // let id = this.props.alarmTriggerId;
                // let data = { handleResult, handleRemark, id }
                // postAction("/bigdata/alarmTrigger/handleAlarm", data).then(
                //     (res) => {
                //         if(res.success) {
                //             message.success("处理成功！");
                //             this.props.onOk();
                //         } else {
                //             message.error(res.msg);
                //         }
                //     }
                // )
            }
        })
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <Modal
                    title={'预警处理'}
                    onCancel={this.props.onCancel}
                    onOk={this.saveAction}
                    visible={true}
                    width={600}
                >
                <FormItem {...formItemLayout} label="提醒模块">
                    {getFieldDecorator('handleResult', {
                        rules: [{
                            required: true,
                            message: '请选择提醒模块！'
                        }]
                    })(
                        <RadioGroup>
                            <Row>
                                {handleResultTypes.map(item=>(
                                    <Col span={8} key={item}><Radio value={item}>{item}</Radio></Col>
                                ))}
                            </Row>
                        </RadioGroup>
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="其他原因">
                    {getFieldDecorator('handleRemark', {
                        rules: [{
                            required: true,
                            message: '请填写其他原因！'
                        }]
                    })(
                        <Input.TextArea style={{width:398,height:138,resize:'none'}}/>
                    )}
                </FormItem>
            </Modal>
        )
    }
}

const HandleStateModalForm = Form.create()(HandleStateModal)
export default HandleStateModalForm