import React from 'react';
import { Card } from 'antd';
import { withRouter } from 'react-router-dom'
import Filter from './Filter';
import TableComponent from './TableComponent';
import IconFont from 'src/iconfont';
import styles from './index.module.less';
class Index extends React.Component {
    state = {
        filterValue: {},
    }

    getFilterValue = (filterValue) => {
		this.setState({filterValue})
    }

    render(){
        return (
            <Card
                className={styles.mainCard}
                title={<div className={styles.cardTitle}>预警列表</div>}
                extra={<div style={{color: '#1890ff', cursor: 'pointer'}} onClick={() => {this.props.history.push('/main/zongheyujing/yujingliebiao/zongtigaikuang')}}><IconFont type="icon-areachart" style={{marginRight: 5}}/>总体概况</div>}
                style={{margin: 20}}
                bordered={false}
            >
                <Filter getFilterValue={this.getFilterValue}/>
                <TableComponent filterValue={this.state.filterValue}/>
            </Card>
        )
    }
}

export default withRouter(Index);