import React, {Component} from 'react';
import {Select, DatePicker, Input, Button, Row, Col} from 'antd';
// import {postAction} from "src/axios";
const Option = Select.Option;
const RangePicker = DatePicker.RangePicker;

export default class Filter extends Component {

    constructor(props) {
        super(props);
        this.state = {
            // 院系
            curCollege:'',
            collegeLists:[],
            // 专业
            curMajor:'',
            majorLists:[],
            // 年级
            curGrade:'',
            gradeLists:[],
            // 预警类别
            curType:'',
            typeLists:[
                { name: '全部预警类别', value: '' },
                { name: '失联预警', value: 1 },
                { name: '网络预警', value: 2 },
                { name: '挂科预警', value: 3 },
                { name: '消费预警', value: 4},
                { name: '夜归预警', value: 5},
                { name: '逃课预警', value: 6}
            ],
            // 预警级别
            curLevel:'',
            levelLists: [
                { name: '全部预警级别', value: '' },
                { name: '红色预警', value: 1 },
                { name: '橙色预警', value: 2 },
                { name: '黄色预警', value: 3 },
                { name: '蓝色预警', value: 4 }
            ],
            // 预警状态
            curStatus: '',
            statusLists: [
                { name: '全部预警状态', value: '' },
                { name: '未处理', value: 0 },
                { name: '已处理', value: 1 }
            ],
            // 学号
            studentNo:'',
            startime: '',
            endTime: '',
        };
    }

    componentDidMount() {
        this.getColledgeLists()
        this.getMajorLists();
    	this.getGradeLists()
    	this.searchAction()
    }
    
    // 筛选条件确定按钮
    searchAction = () => {
    	let filterValue={
    		curCollege: this.state.curCollege,
    		curMajor:this.state.curMajor,
            curGrade:this.state.curGrade,
            curType:this.state.curType,
            curLevel:this.state.curLevel,
            curStatus:this.state.curStatus,
            studentNo: this.state.studentNo
    	}
    	
    	this.props.getFilterValue(filterValue);
    }
    
    
    // 获取年级list
    getGradeLists() {
        this.setState({
            gradeLists: [
                {name: '2019年级', value: '2019'},
                {name: '2018年级', value: '2018'},
                {name: '2017年级', value: '2017'},
            ],
            curGrade: '2019'
        })
    	// postAction(
    	// 	"/bigdata/school/getGradeList",
    	// 	{
    	// 		year: this.state.curYear,
    	// 		collegeCode: this.state.curCollege,
    	// 		majorCode: this.state.curMajor
    	// 	}
    	// ).then(
    	// 	(res) =>{
    	// 		if (res.success){
    	// 			this.setState({
    	// 				gradeLists: res.obj,
    	// 			})
    	// 		}
    	// 	}
    	// )
    }
    
    // 获取专业list
    getMajorLists () {
        this.setState({
            majorLists: [
                {name: '全部专业', value: ''},
                {name: '西方经济学', value: '1'},
                {name: '国际金融', value: '2'},
            ],
        })
    	// postAction(
    	// 	"/bigdata/school/listMajorByCollege",
    	// 	{
    	// 		collegeCode: this.state.curCollege
    	// 	}
    	// ).then(
    	// 	(res) => {
    	// 		if (res.success) {
    	// 			this.setState({
    	// 				majorLists: res.obj,
    	// 			})
    	// 		}
    	// 	}
    	// )
    }
    
    // 获取学院list
    getColledgeLists(){
        this.setState({
            collegeLists: [
                {name: '全部学院', value: ''},
                {name: '经济学院', value: '1'},
                {name: '法学院', value: '2'},
            ]
        })
    	// postAction(
    	// 	"/bigdata/school/listCollege",
    	// ).then(
    	// 	(res)=>{
    	// 		if (res.success) {
    	// 			this.setState({
    	// 				collegeLists: res.obj,
    	// 			})
    	// 		}
    	// 	}
    	// )
    }
    
    changeFilter(num, value){
        console.log()
    	if (num === 0){ // 变更年级
    		this.setState({
                curGrade: value,
            },() =>{
                // this.getMajorLists()
                // this.getGradeLists()
                // this.getClassLists()
            })
    	} else if (num === 1){ // 变更院系
    		this.setState({
                curCollege: value,
            },
            () => {
                // this.getGradeLists()
                // this.getClassLists()
            })
    	} else if (num === 2){ // 变更专业
    		this.setState({
                curMajor: value,
            },() => {
                // this.getClassLists()
            })
    	} else if (num === 3){ // 变更预警类别
    		this.setState({
    			curType: value,
    		})
    	} else if (num === 4){ // 变更预警级别
    		this.setState({
    			curLevel: value,
    		})
    	} else if (num === 5){ // 变更预警状态
    		this.setState({
    			curStatus: value,
    		})
        }
    }

    changeStudentNo(e){
        this.setState({studentNo: e.target.value});
    }

    changeTime(date, dateString){
        this.setState({
            startime: dateString[0],
            endtime: dateString[1]
        })
    }

    handleExport = () => {
        
    }

    render () {
        return (
            <Row style={{lineHeight: '45px'}}>
                <Col span={8}>
                    <Row style={{display: 'flex', alignItems: 'center'}}>
                        <Col span={8} style={{paddingRight: 20, textAlign: 'right'}}>年级：</Col>
                        <Col span={16}>
                            <Select style={{ width: '100%' }}
                                value={this.state.curGrade}
                                onChange={this.changeFilter.bind(this, 0)}
                            >
                                {
                                    this.state.gradeLists.map((item, index) => <Option key={index} value={item.value}>{item.name}</Option>)
                                }
                            </Select>
                        </Col>
                    </Row>
                </Col>
                <Col span={8}>
                    <Row style={{display: 'flex', alignItems: 'center'}}>
                        <Col span={8} style={{paddingRight: 20, textAlign: 'right'}}>院系：</Col>
                        <Col span={16}>
                            <Select style={{ width: '100%' }}
                                value={this.state.curCollege}
                                onChange={this.changeFilter.bind(this, 1)}
                            >
                                {
                                    this.state.collegeLists.map((item, index) => <Option key={index} value={item.value}>{item.name}</Option>)
                                }
                            </Select>
                        </Col>
                    </Row>
                </Col>
                <Col span={8}>
                    <Row style={{display: 'flex', alignItems: 'center'}}>
                        <Col span={8} style={{paddingRight: 20, textAlign: 'right'}}>专业：</Col>
                        <Col span={16}>
                            <Select style={{ width: '100%' }}
                                value={this.state.curMajor}
                                onChange={this.changeFilter.bind(this, 2)}
                            >
                                {
                                    this.state.majorLists.map((item, index) => <Option key={index} value={item.value}>{item.name}</Option>)
                                }
                            </Select>
                        </Col>
                    </Row>
                </Col>
                <Col span={8}>
                    <Row style={{display: 'flex', alignItems: 'center'}}>
                        <Col span={8} style={{paddingRight: 20, textAlign: 'right'}}>预警类别：</Col>
                        <Col span={16}>
                            <Select style={{ width: '100%' }}
                                value={this.state.curType}
                                onChange={this.changeFilter.bind(this, 3)}
                            >
                                {
                                    this.state.typeLists.map((item, index) => <Option key={index} value={item.value}>{item.name}</Option>)
                                }
                            </Select>
                        </Col>
                    </Row>
                </Col>
                <Col span={8}>
                    <Row style={{display: 'flex', alignItems: 'center'}}>
                        <Col span={8} style={{paddingRight: 20, textAlign: 'right'}}>预警级别：</Col>
                        <Col span={16}>
                            <Select style={{ width: '100%' }}
                                value={this.state.curLevel}
                                onChange={this.changeFilter.bind(this, 4)}
                            >
                                {
                                    this.state.levelLists.map((item, index) => <Option key={index} value={item.value}>{item.name}</Option>)
                                }
                            </Select>
                        </Col>
                    </Row>
                </Col>
                <Col span={8}>
                    <Row style={{display: 'flex', alignItems: 'center'}}>
                        <Col span={8} style={{paddingRight: 20, textAlign: 'right'}}>预警状态：</Col>
                        <Col span={16}>
                            <Select style={{ width: '100%' }}
                                value={this.state.curStatus}
                                onChange={this.changeFilter.bind(this, 5)}
                            >
                                {
                                    this.state.statusLists.map((item, index) => <Option key={index} value={item.value}>{item.name}</Option>)
                                }
                            </Select>
                        </Col>
                    </Row>
                </Col>
                <Col span={8}>
                    <Row style={{display: 'flex', alignItems: 'center'}}>
                        <Col span={8} style={{paddingRight: 20, textAlign: 'right'}}>学号/姓名：</Col>
                        <Col span={16}>
                            <Input
                                value={this.state.studentNo}
                                style={{ width: '100%' }}
                                placeholder="请输入学生学号或一卡通号或姓名"
                                onChange={this.changeStudentNo.bind(this)} />
                        </Col>
                    </Row>
                </Col>
                <Col span={16}>
                    <Row style={{display: 'flex', alignItems: 'center'}}>
                        <Col span={4} style={{paddingRight: 20, textAlign: 'right'}}>时间：</Col>
                        <Col span={14}>
                            <RangePicker
                                style={{ width: '100%' }}
                                onChange={this.changeTime.bind(this)} 
                                />
                        </Col>
                        <Col span={4}>
                            <Button icon="search" type={'primary'} style={{display: 'block', margin: '0 auto'}} onClick={this.searchAction}>搜索</Button>
                        </Col>
                        <Col span={2}>
                            <Button type={'primary'} onClick={this.handleExport} >导出</Button>
                        </Col>
                    </Row>
                </Col>
            </Row>
        )
    }
}
